import React, { useState, useEffect } from "react";
import Axios from "axios";
import { nodeApiLink } from "../config/NodeServerLink";
import { CourseInprogress } from "../Component/CourseInprogress";
import { useSelector } from "react-redux";
import { useHistory } from "react-router-dom";
import Fade from "react-reveal/Fade";
import '../scssFile/Inpogress.css'

export const Inprogress = () => {
    const userOwnCourses =
        useSelector((state) => state.userStatus.userInfo && state.userStatus.userInfo.ownCourses) || [];
    const auth = useSelector((state) => state.userStatus.auth);
    const history = useHistory();
    const [ownCourses, setOwnCourses] = useState([]);
    useEffect(() => {
        const getOwnCourses = async () => {
            const { data } = await Axios.get(`${nodeApiLink}/api/user/owncourse`, { withCredentials: true });
            setOwnCourses(data);
        };
        getOwnCourses();
        return () => {
            //
        };
    }, [userOwnCourses.length]);
    useEffect(() => {
        if (!auth) history.push("/");
        return () => {
            //
        };
    }, [auth, history]);

    return (
        <div
            className="container-fluid"
            style={{
                minHeight: 700,
                backgroundColor: "#f3f3f3",
                display: "flex",
                justifyContent: "center",
                flexDirection: "column",
            }}
        >
            {ownCourses &&
                ownCourses.map((course,index) => {
                    return (
                        <Fade key={course._id} bottom>
                            <CourseInprogress  course={course} />
                        </Fade>
                    );
                })}
            {/* {ownCourses.length <0 
                ? ownCourses.map((course) => {
                      return <CourseInprogress key={course._id} course={course} />;
                  })
                : (<Skeleton variant='rect' width={956} height={357} style={{margin:'32px auto'}} />)} */}
        </div>
    );
};
export default Inprogress;
